.language-selector {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1000;
}

.language-button {
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #8B4513;
  color: #FFD700;
  padding: 8px 16px;
  cursor: pointer;
  font-family: inherit;
  font-size: 14px;
  border-radius: 4px;
  transition: all 0.2s ease;
  min-width: 120px;
}
.language-button:hover {
  background: rgba(0, 0, 0, 0.9);
  border-color: #DAA520;
  color: #FFFFFF;
}

.language-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  border: 2px solid #8B4513;
  border-top: none;
  border-radius: 0 0 4px 4px;
  min-width: 120px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.language-option {
  display: block;
  width: 100%;
  background: transparent;
  border: none;
  color: #FFD700;
  padding: 8px 16px;
  cursor: pointer;
  font-family: inherit;
  font-size: 14px;
  text-align: left;
  transition: all 0.2s ease;
}
.language-option:hover {
  background: rgba(139, 69, 19, 0.3);
  color: #FFFFFF;
}
.language-option.active {
  background: rgba(139, 69, 19, 0.5);
  color: #FFFFFF;
  font-weight: bold;
}

.App.started .language-selector {
  display: none;
}