body, #root, .App {
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.App {
  background-color: #000;
}
.App.dropping {
  background-color: #111;
}

.Body, .BodyV {
  position: absolute;
  top: 0;
  bottom: 0;
  height: auto;
  left: calc(50% - 66.6666666667vh);
  width: 133.3333333333vh;
}

.Body .inner {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

@media (max-aspect-ratio: 640/480) {
  .Body {
    left: 0;
    right: 0;
    width: auto;
    top: calc(50% - 37.5vw);
    height: 75vw;
  }
  .BodyV {
    left: 0;
    right: 0;
    width: auto;
  }
}
.BodyV {
  pointer-events: none;
}

@media (min-aspect-ratio: 3/1) {
  .App.keyboard .Body .inner {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 75vw;
    height: 56.25vw;
    transform: translate(-50%, -70%);
  }
}
.App canvas {
  width: 100%;
  height: 100%;
  cursor: none;
  visibility: hidden;
}
.App.started canvas {
  visibility: visible;
}
.App .keyboard {
  position: absolute;
  pointer-events: auto;
  background: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  z-index: 100;
  display: none;
  cursor: none;
}
.App .keyboard::selection {
  background: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
}
.App.keyboard .keyboard {
  display: block;
}
.App .start, .App .error, .App .loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.App .start, .App .error {
  max-width: 90%;
  max-height: 90%;
  overflow-y: auto;
  pointer-events: auto;
}
.App .start {
  border: 1px solid #888;
  padding: 12px;
  color: #fff;
  font-size: 18px;
  text-align: center;
  background: #000;
}
.App .start p {
  margin: 10px 0;
}
.App .start .startButton {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #fff;
  background: #000;
  font-size: 2em;
  padding: 4px 18px;
  cursor: pointer;
  margin-top: 6px;
  width: 8em;
}
.App .start .startButton:hover {
  background-color: #111;
}
.App .start .saveList {
  border: 1px solid #fff;
  text-align: left;
}
.App .start .saveList li {
  padding: 0 6px;
}
.App .start .saveList li .info {
  color: #888;
  margin-left: 6px;
}
.App .start .saveList li .btnRemove {
  color: #800;
}
.App .start .saveList li .btnRemove:hover {
  color: #f00;
}
.App .start .saveList li .btnRemove {
  float: right;
  cursor: pointer;
  margin: 0 4px;
}
.App .start .saveList li .btnDownload {
  color: #888;
}
.App .start .saveList li .btnDownload:hover {
  color: #fff;
}
.App .start .saveList li .btnDownload {
  float: right;
  cursor: pointer;
  margin: 0 4px;
}
.App .start .saveList li:hover {
  background-color: #444;
}

.App .error {
  display: block;
  border: 1px solid #800;
  background: #300;
  padding: 12px;
  color: #f33;
  font-size: 18px;
  text-align: center;
}
.App .error:hover {
  text-decoration: none;
}
.App .error .header {
  font-weight: bold;
  font-size: 1.2em;
  margin-bottom: 0.3em;
}
.App .error .body {
  font-family: monospace;
}
.App .error .footer {
  margin-top: 0.5em;
  font-size: 0.8em;
}
.App .loading {
  color: #888;
  font-size: 32px;
  text-align: center;
  width: 75%;
}
.App .loading .progressBar {
  display: block;
  position: absolute;
  margin-top: 16px;
  width: 100%;
  height: 40px;
  border: 2px solid #888;
}
.App .loading .progressBar span {
  position: absolute;
  left: 3px;
  top: 3px;
  bottom: 3px;
  right: 3px;
}
.App .loading .progressBar span span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #888;
}
.App a, .App .link {
  display: inline-block;
  color: rgb(112, 112, 255);
  text-decoration: none;
}
.App a:hover, .App .link:hover {
  text-decoration: underline;
}
.App a, .App .link {
  cursor: pointer;
}

.App .touch-ui {
  display: none;
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 25vh;
}
.App .touch-ui .touch-button {
  position: absolute;
  left: 50%;
  background-color: #444;
  transform: translate(-50%, -50%);
  pointer-events: auto;
}
.App .touch-ui .touch-button.active {
  background-color: #fff;
}
.App .touch-ui .touch-button {
  mask-size: 100% 100%;
}
.App .touch-ui .touch-button-0 {
  top: 15vh;
}
.App .touch-ui .touch-button-1 {
  top: 35vh;
}
.App .touch-ui .touch-button-2 {
  top: 55vh;
}
.App .touch-ui .touch-button-3 {
  top: 70vh;
  left: 5vw;
}
.App .touch-ui .touch-button-4 {
  top: 70vh;
  left: 11vw;
}
.App .touch-ui .touch-button-5 {
  top: 85vh;
  left: 5vw;
}
.App .touch-ui .touch-button-6 {
  top: 85vh;
  left: 11vw;
}
.App .touch-ui.touch-mods {
  left: 0;
}
.App .touch-ui.touch-mods .touch-button {
  width: 15vh;
  height: 15vh;
  border-radius: 10vh;
}
.App .touch-ui.touch-mods .touch-button-0 {
  mask-image: url(./icons/move.svg);
}
.App .touch-ui.touch-mods .touch-button-1 {
  mask-image: url(./icons/rmb.svg);
}
.App .touch-ui.touch-mods .touch-button-2 {
  mask-image: url(./icons/shift.svg);
}
.App .touch-ui.touch-mods .touch-button-3 {
  mask-image: url(./icons/f5.svg);
}
.App .touch-ui.touch-mods .touch-button-4 {
  mask-image: url(./icons/f6.svg);
}
.App .touch-ui.touch-belt {
  right: 0;
}
.App .touch-ui.touch-belt .touch-button {
  width: 12vh;
  height: 12vh;
  font-size: 1vh;
  border-radius: 20%;
  overflow: hidden;
  background-color: #000;
  border: 2px solid #444;
}
.App .touch-ui.touch-belt .touch-button canvas {
  position: absolute;
  left: 5%;
  top: 5%;
  width: 90%;
  height: 90%;
}
.App .touch-ui.touch-belt .touch-button::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 20%;
  box-shadow: inset 0 0 1em 1.5em #000;
}
.App .touch-ui.fkeys-left {
  left: 0;
  top: auto;
  bottom: 0;
  height: 35vh;
}
.App .touch-ui.fkeys-left .touch-button {
  width: 15vh;
  height: 15vh;
  position: absolute;
  left: 50%;
  background-color: #444;
  transform: translate(-50%, -50%);
  pointer-events: auto;
}
.App .touch-ui.fkeys-left .touch-button.active {
  background-color: #fff;
}
.App .touch-ui.fkeys-left .touch-button {
  mask-size: 100% 100%;
}
.App .touch-ui.fkeys-left .touch-button-3 {
  top: 0;
  transform: translate(-50%, 0);
  mask-image: url(./icons/f5.svg);
}
.App .touch-ui.fkeys-left .touch-button-4 {
  top: 60%;
  mask-image: url(./icons/f6.svg);
}
.App .touch-ui.fkeys-right {
  left: auto;
  top: auto;
  right: 0;
  bottom: 0;
  height: 35vh;
}
.App .touch-ui.fkeys-right .touch-button {
  width: 15vh;
  height: 15vh;
  position: absolute;
  left: 50%;
  background-color: #444;
  transform: translate(-50%, -50%);
  pointer-events: auto;
}
.App .touch-ui.fkeys-right .touch-button.active {
  background-color: #fff;
}
.App .touch-ui.fkeys-right .touch-button {
  mask-size: 100% 100%;
}
.App .touch-ui.fkeys-right .touch-button-5 {
  top: 0;
  transform: translate(-50%, 0);
  mask-image: url(./icons/f7.svg);
}
.App .touch-ui.fkeys-right .touch-button-6 {
  top: 60%;
  mask-image: url(./icons/f8.svg);
}
.App.touch .touch-ui {
  display: block;
}
.App.touch.keyboard .touch-ui {
  display: none;
}

@media (max-aspect-ratio: 880/480) {
  .App .touch-ui {
    width: calc(50vw - 66.6666666667vh);
  }
}
@media (max-aspect-ratio: 832/480) {
  .App .touch-ui {
    width: 20vh;
  }
}
@media (max-aspect-ratio: 640/480) {
  .App .touch-ui {
    width: auto;
    height: 20vw;
  }
  .App .touch-ui .touch-button {
    top: 55%;
  }
  .App .touch-ui .touch-button-0 {
    left: 16.7vw;
  }
  .App .touch-ui .touch-button-1 {
    left: 50vw;
  }
  .App .touch-ui .touch-button-2 {
    left: 83.3vw;
  }
  .App .touch-ui.touch-mods {
    top: auto;
    right: 0;
  }
  .App .touch-ui.touch-mods .touch-button {
    width: 20vw;
    height: 20vw;
    border-radius: 10vw;
  }
  .App .touch-ui.touch-belt {
    bottom: auto;
    left: 0;
  }
  .App .touch-ui.touch-belt .touch-button {
    width: 14vw;
    height: 14vw;
    font-size: 1vw;
  }
  .App .touch-ui.fkeys-left {
    left: 0;
    bottom: 15vh;
    width: 50%;
    height: 12vh;
  }
  .App .touch-ui.fkeys-left .touch-button {
    height: 12vh;
    width: 12vh;
  }
  .App .touch-ui.fkeys-left .touch-button-3 {
    left: 25%;
  }
  .App .touch-ui.fkeys-left .touch-button-4 {
    top: 50%;
    left: 75%;
  }
  .App .touch-ui.fkeys-right {
    left: auto;
    right: 0;
    bottom: 15vh;
    width: 50%;
    height: 12vh;
  }
  .App .touch-ui.fkeys-right .touch-button {
    height: 12vh;
    width: 12vh;
  }
  .App .touch-ui.fkeys-right .touch-button-5 {
    left: 25%;
  }
  .App .touch-ui.fkeys-right .touch-button-6 {
    top: 50%;
    left: 75%;
  }
}
@media (max-aspect-ratio: 640/736) {
  .App .touch-ui {
    height: calc(50vh - 37.5vw);
  }
}
@media (max-aspect-ratio: 640/800) {
  .App .touch-ui {
    height: 35vw;
  }
}